import TonLogo from "assets/img/TonLogo.svg";
import twoArrow from "assets/img/twoArrow.svg";
import TonSymbol from "assets/img/ton_symbol.svg";
import LikeSvg from "components/common/LikeSvg";

function ContestPrizes({ activeContest, router, time }) {
  const handleInnerClick = (event) => {
    event.stopPropagation();
  };

  const handleAcceptInnerClick = () => {
    router.toView("main");
  };

  return (
    <div className="ContestPrize roundedBlock_greenBroder">
      <div className="ContestPrizes">
        <div className="ContestPrizes__item">
          {activeContest.type == "ended" ? (
            activeContest.price ? (
              <div className="ContestPrizes__item">
                <div className="ContestPrize__logo">
                  <img src={TonLogo} />
                </div>

                <div className="ContestsItemPrize__title title_h4-18px">
                  NFT  {" "}
                  <a
                    className="text_accented underline"
                    href="https://getgems.io/"
                    target="_blank"
                    onClick={handleInnerClick}
                  >
                    GetGems
                  </a>
                  {`  ${activeContest.price} `}
                  <img src={TonSymbol} />
                </div>
              </div>
            ) : (
              <div className="ContestPrizes__item">
                <div className="ContestPrize__logo">
                  <img src={TonLogo} />
                </div>
                <div className="ContestsItemPrize__title title_h4-18px">
                  NFT  {" "}
                  <a
                    className="text_accented underline"
                    href="https://getgems.io/"
                    onClick={handleInnerClick}
                    target="_blank"
                  >
                    GetGems
                  </a>
                </div>
              </div>
            )
          ) : (
            <div className="ContestPrizes__item">
              <div className="ContestPrize__logo">
                <img src={TonLogo} />
              </div>
              <div className="ContestsItemPrize__title title_h4-18px">
                  1 
                <br />
                 NFT {" "}
                <a
                  href="https://getgems.io/"
                  target="_blank"
                  onClick={handleInnerClick}
                  className="text_accented underline"
                >
                  GetGems
                </a>
              </div>
            </div>
          )}
        </div>
      </div>

      {activeContest.type !== "ended" && (
        <>
          <div className="ContestPrize__fullDelimetr"></div>
          <div className="ContestPrize__voting" onClick={handleInnerClick}>
            {activeContest.type == "vote" ? (
              <>
                <LikeSvg full="true" />
                <span className="ContestPrize__voting_text">
                   ,   
                </span>
              </>
            ) : (
              <>
                <div className="ContestPrize__plug"></div>
                <a
                  className="ContestPrize__voting_text underline text_accented"
                  onClick={handleAcceptInnerClick}
                >
                     
                </a>
              </>
            )}
            <img
              src={twoArrow}
              className={`${!time && "rotateImg"} ${
                activeContest.type == "workAcceptance" && "workAcceptance"
              }`}
            />
          </div>
        </>
      )}
      {
        <div className="ContestItem__date transparentBlock">
          {activeContest.type == "workAcceptance"
            ? "  "
            : activeContest.type == "vote"
            ? " "
            : " "}
          {activeContest.type == "ended" ? "" : " 18 "}
        </div>
      }
    </div>
  );
}

export default ContestPrizes;
